.nav{
    position: fixed;
    top:0;
    padding: 20px;
    width: 100%;
    height:30px;
    z-index:1;
    /*transition*/
    transition-timing-function: ease-in;
    transition:  all 0.5s;;
}

.nav__contents{
    display: flex;
    justify-content: space-between;
}
.Nav_logo{
    position: fixed;
    /* right: 20px; */
    top:10px;
    left:0;
    width:80px;
    object-fit:contain;
    padding-left:20px;
    cursor:pointer;
}
.nav_avatar{
    position: fixed;
    object-fit:contain;

    /* right: 20px; */
    right:20px;
    width:30px;

}
.nav__black{
    background-color: #111;
}