.loginScreen__logo{
    position:fixed;
    left:0;
    width: 150px;
    object-fit: contain;
    padding-left:20px;
}
.loginScreen{
    position: relative;
    height:100%;
    background:url("https://blog.vpncity.com/wp-content/uploads/2020/01/xx.png")center no-repeat;
    background-size: cover;

}
.loginScreen__button{
    position: fixed;
    right:20px;
    padding:10px 20px;
    top:20px;
    font-size:1rem;
    color:#fff;
    background-color:#e50914;
    font-weight:600;
    border:none;
    cursor:pointer;
}
.loginScreen_gradient{
    width:100%;
    z-index:1;
    height:100vh;
    background: rgba(0,0,0,0.4);
}